import React from "react";
import "./Progress.css";
import { skillsSection } from "../../portfolio";
import { Fade } from "react-reveal";

export default function StackProgress() {
    if(skillsSection.viewSkillBars){
        return (
            <div className="skills-container" id = "skills">
                
        <h1 className="skills-title">{skillsSection.title}</h1>
                <hr className="horizontal-line"></hr>
                <div className="skills-main">
                <h1 className="skills-heading">{skillsSection.softwareSkills.tab}</h1>
                <div className="skills-tab">
                    <Fade bottom duration={1000} distance="20px">
                    <div className="skills-bar">
                        <h2 className="skills-heading">{skillsSection.softwareSkills.title1}</h2>
                        {skillsSection.softwareSkills.category1.map((exp) => {
                            const progressStyle = {
                                width: exp.Level
                            };
                            return (
                            <div className="skill">
                                <p>{exp.Name}</p>
                                <div className="meter_blue">
                                    <span style={progressStyle}></span>
                                </div>
                            </div>
                            );
                        })}
                    </div>
                    </Fade>
                    <hr className="horizontal-line"></hr>
                    <Fade bottom duration={2000} distance="40px">
                    <div className="skills-bar">
                        <h2 className="skills-heading">{skillsSection.softwareSkills.title2}</h2>
                        {skillsSection.softwareSkills.category2.map((exp) => {
                            const progressStyle = {
                                width: exp.Level
                            };
                            return (
                            <div className="skill">
                                <p>{exp.Name}</p>
                                <div className="meter_lightgreen">
                                    <span style={progressStyle}></span>
                                </div>
                            </div>
                            );
                        })}
                    </div>
                    </Fade>
                    <hr className="horizontal-line"></hr>
                    <Fade bottom duration={3000} distance="60px">
                    <div className="skills-bar">
                        <h2 className="skills-heading">{skillsSection.softwareSkills.title3}</h2>
                        {skillsSection.softwareSkills.category3.map((exp) => {
                            const progressStyle = {
                                width: exp.Level
                            };
                            return (
                            <div className="skill">
                                <p>{exp.Name}</p>
                                <div className="meter_green">
                                    <span style={progressStyle}></span>
                                </div>
                            </div>
                            );
                        })}
                    </div>
                    </Fade>
                </div>
                <h1 className="skills-heading">{skillsSection.hardwareSkills.tab}</h1>
                <div className="skills-tab">
                    <Fade bottom duration={1000} distance="20px">
                    <div className="skills-bar">
                        <h2 className="skills-heading">{skillsSection.hardwareSkills.title1}</h2>
                        {skillsSection.hardwareSkills.category1.map((exp) => {
                            const progressStyle = {
                                width: exp.Level
                            };
                            return (
                            <div className="skill">
                                <p>{exp.Name}</p>
                                <div className="meter_red">
                                    <span style={progressStyle}></span>
                                </div>
                            </div>
                            );
                        })}
                    </div>
                    </Fade>
                    <hr className="horizontal-line"></hr>
                    <Fade bottom duration={2000} distance="40px">
                    <div className="skills-bar">
                        <h2 className="skills-heading">{skillsSection.hardwareSkills.title2}</h2>
                        {skillsSection.hardwareSkills.category2.map((exp) => {
                            const progressStyle = {
                                width: exp.Level
                            };
                            return (
                            <div className="skill">
                                <p>{exp.Name}</p>
                                <div className="meter_purple">
                                    <span style={progressStyle}></span>
                                </div>
                            </div>
                            );
                        })}
                    </div>
                    </Fade>
                    <hr className="horizontal-line"></hr>
                    <div className="skills-bar"></div>
                </div>

                <h1 className="skills-heading">{skillsSection.miscellaneous.tab}</h1>
                <div className="skills-tab">
                    <Fade bottom duration={1000} distance="20px">
                    <div className="skills-bar">
                        <h2 className="skills-heading">{skillsSection.miscellaneous.title1}</h2>
                        {skillsSection.miscellaneous.category1.map((exp) => {
                            const progressStyle = {
                                width: exp.Level
                            };
                            return (
                            <div className="skill">
                                <p>{exp.Name}</p>
                                <div className="meter_yellow">
                                    <span style={progressStyle}></span>
                                </div>
                            </div>
                            );
                        })}
                    </div>
                    </Fade>
                    <hr className="horizontal-line"></hr>
                    <div className="skills-bar"></div>
                    <div className="skills-bar"></div>
                </div>
                </div>
            </div>
        );
    }
    return null;
}
