import React from "react";
import "./Thanks.css";
import { thanks } from "../../portfolio";
import {Fade} from "react-reveal";

export default function Thanks() {
    return (
      <Fade bottom duration={1000} distance="40px">
      <div className="thanks-container" id="thanks">
        <h1 className="thanks-heading">{thanks.title}</h1>
        <div className="thanks-main">
          {thanks.thanks.map((instance)=>{
            return(
              <div className="thanks-instance">
                <h3>{instance.who}</h3>
                <h4>{instance.what}</h4>
                <p>{instance.for}</p>
                <a href={instance.ref}>{instance.type}</a>
              </div>
            );
          })}
        </div>
      </div>
      </Fade>
    );
  }