import React from 'react';
import "./Showcase.css";
import { showCase } from "../../portfolio";
import Popup from "reactjs-popup";
import {Fade} from "react-reveal";


export default function Showcase() {
    var curTag = "all";

    function setVisible(className){
      var rob_doc = document.getElementsByClassName(className);
      for (var i = 0; i < rob_doc.length; i ++) {
          rob_doc[i].style.display = 'inline';
      }
    }

    function setInvisible(className){
      var rob_doc = document.getElementsByClassName(className);
      for (var i = 0; i < rob_doc.length; i ++) {
          rob_doc[i].style.display = 'none';
      }
    }

    function robBtn(e){
      if(curTag!="Robotics"){
        curTag = "Robotics";
        document.getElementById("robButton").style.backgroundColor = "rgb(255,102,102)";
        document.getElementById("devButton").style.backgroundColor = "rgb(192,192,192)";
        document.getElementById("othButton").style.backgroundColor = "rgb(192,192,192)";
        setVisible('showcase-instance-outer-Robotics');
        setInvisible('showcase-instance-outer-Developer');
        setInvisible('showcase-instance-outer-Others');
      }
      else{
        curTag = "all"; 
        document.getElementById("robButton").style.backgroundColor = "rgb(192,192,192)";
        document.getElementById("devButton").style.backgroundColor = "rgb(192,192,192)";
        document.getElementById("othButton").style.backgroundColor = "rgb(192,192,192)";
        setVisible('showcase-instance-outer-Robotics');
        setVisible('showcase-instance-outer-Developer');
        setVisible('showcase-instance-outer-Others');
      }
    }
    function devBtn(e){
      if(curTag!="Developer"){
        curTag = "Developer";
        document.getElementById("robButton").style.backgroundColor = "rgb(192,192,192)";
        document.getElementById("devButton").style.backgroundColor = "rgb(102,255,102)";
        document.getElementById("othButton").style.backgroundColor = "rgb(192,192,192)";
        setInvisible('showcase-instance-outer-Robotics');
        setVisible('showcase-instance-outer-Developer');
        setInvisible('showcase-instance-outer-Others');
      }
      else{
        curTag = "all"; 
        document.getElementById("robButton").style.backgroundColor = "rgb(192,192,192)";
        document.getElementById("devButton").style.backgroundColor = "rgb(192,192,192)";
        document.getElementById("othButton").style.backgroundColor = "rgb(192,192,192)";
        setVisible('showcase-instance-outer-Robotics');
        setVisible('showcase-instance-outer-Developer');
        setVisible('showcase-instance-outer-Others');
      }
    }
    function othBtn(e){
      if(curTag!="Others"){
        curTag = "Others";
        document.getElementById("robButton").style.backgroundColor = "rgb(192,192,192)";
        document.getElementById("devButton").style.backgroundColor = "rgb(192,192,192)";
        document.getElementById("othButton").style.backgroundColor = "rgb(102,102,255)";
        setInvisible('showcase-instance-outer-Robotics');
        setInvisible('showcase-instance-outer-Developer');
        setVisible('showcase-instance-outer-Others');
      }
      else{
        curTag = "all"; 
        document.getElementById("robButton").style.backgroundColor = "rgb(192,192,192)";
        document.getElementById("devButton").style.backgroundColor = "rgb(192,192,192)";
        document.getElementById("othButton").style.backgroundColor = "rgb(192,192,192)";
        setVisible('showcase-instance-outer-Robotics');
        setVisible('showcase-instance-outer-Developer');
        setVisible('showcase-instance-outer-Others');
      }
    }
    return (
      <Fade bottom duration={1000} distance="40px">
      <div className="showcase-container" id="showcase">
        <h1 className="showcase-heading">{showCase.title}</h1>
        <hr className="horizontal-line"></hr>
        <div className="showcase-main">
          <div className="showcase-tag-div">
            <div onClick={robBtn} id="robButton" className="tag-instance-div">
                <p>{showCase.tag[0]}</p>
            </div>
            <div onClick={devBtn} id="devButton" className="tag-instance-div">
                <p>{showCase.tag[1]}</p>
            </div>
            <div onClick={othBtn} id="othButton" className="tag-instance-div">
                <p>{showCase.tag[2]}</p>
            </div>
          </div>
        
          <hr className="horizontal-line"></hr>

          <div className="showcase-content-div">
          {showCase.showcase.map((instance)=>{
              return(
                <div className={"showcase-instance-outer-"+instance.tag}>
                <Popup trigger={<div className="showcase-instance-div">
                                  <img alt="Showcase image" src={instance.image}></img>
                                  <div class="showcase-instance-overlay">
                                    <div class="showcase-instance-inneroverlay">
                                    <h2>{instance.title}</h2>
                                    <h3>{instance.tag}</h3>
                                    </div>
                                  </div>
                                </div>}
                        contentStyle={{width:'70%'}}
                        modal>
                  {close => (
                    <div className="modal">
                      <a className="close" onClick={close}>
                        &times;
                      </a>
                      <div className="header">
                        <h2>
                          {instance.title}
                        </h2>
                        <p>
                          {instance.tag}
                        </p>
                      </div>
                      <div className="content">
                        <div className="content-text">
                          <div>
                            {instance.content.map((text)=>{
                              return(
                                <p>{text}</p>
                              );
                            })}
                            {
                              instance.reference != null
                              &&
                            <a href={instance.reference}>{instance.reftext}</a>
                            }
                          </div>
                          <div className="keywords">
                          <p>Keywords: {instance.keywords}</p>
                          </div>
                        </div>
                        <div className="content-image">
                          <img alt="Showcase image" src={instance.image}></img>
                        </div>
                      </div>
                      <div className="actions">
                        <button
                          className="button"
                          onClick={() => {
                            console.log("modal closed ");
                            close();
                          }}
                        >
                          CLOSE
                        </button>
                      </div>
                    </div>
                  )}
                </Popup>
                </div>
              );
            })}
          </div>
        </div>
      </div>
      </Fade>
    );
  }