import React from "react";
import "./Experience.css";
import { experience } from "../../portfolio";
import {Fade} from "react-reveal";
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import SchoolIcon from '@material-ui/icons/School';
import WorkIcon from '@material-ui/icons/Work';
import StarIcon from '@material-ui/icons/Star';

export default function Experiences() {
    return (
<div className="experience-container" id="experience">
<Fade bottom duration={1000} distance="40px">
<h1 className="experience-heading">{experience.title}</h1>
</Fade>
<div className="experience-main">
  <VerticalTimeline>
  <VerticalTimelineElement
    className="vertical-timeline-element--work"
    date="2009-2015"
    iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
    icon={<SchoolIcon/>}
  >
    <div className="experience-textbox">
    <h3 className="vertical-timeline-element-title">Suankularb Witthayalai School</h3>
    <h4 className="vertical-timeline-element-subtitle">Gifted and Talented Program</h4>
    <p>
      Junior High School &amp; High School<br></br>GPA: 3.97/4.00
    </p>
    </div>
  </VerticalTimelineElement>
  <VerticalTimelineElement
    className="vertical-timeline-element--work"
    date="2015 - 2019"
    iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
    icon={<SchoolIcon/>}
  >
    <div className="experience-textbox">
    <h3 className="vertical-timeline-element-title">Tohoku University</h3>
    <h4 className="vertical-timeline-element-subtitle">Mechanical Engineering, Biorobotics Course</h4>
    <p>
      Bachelor's Degree <br></br>
      Received President's Award from School of Engineering.<br></br>
      Published and presented a conference paper at SII2020.<br></br>
      GPA: 3.96/4.00
    </p>
    </div>
  </VerticalTimelineElement>
  <VerticalTimelineElement
    className="vertical-timeline-element--work"
    date="August 2018"
    iconStyle={{ background: 'rgb(233, 30, 99)', color: '#fff' }}
    icon={<WorkIcon/>}
  >
    <div className="experience-textbox">
    <h3 className="vertical-timeline-element-title">ROS Engineer @Obodroid</h3>
    <h4 className="vertical-timeline-element-subtitle">Internship (1 month)</h4>
    <p>
      Mobile robot control and navigation development
    </p>
    </div>
  </VerticalTimelineElement>
  <VerticalTimelineElement
    className="vertical-timeline-element--work"
    date="2019 - now"
    iconStyle={{ background: 'rgb(33, 150, 243)', color: '#fff' }}
    icon={<SchoolIcon/>}
  >
    <div className="experience-textbox">
    <h3 className="vertical-timeline-element-title">Tohoku University</h3>
    <h4 className="vertical-timeline-element-subtitle">Robotics Major</h4>
    <p>
      Master's Degree <br></br>
      IREX2019 Robot Exhibition - Joint Research with OLPASO
    </p>
    </div>
  </VerticalTimelineElement>
  </VerticalTimeline>
</div>
</div>
    );
  }