import React from "react";
import "./Hobbies.css";
import { hobbies } from "../../portfolio";
import {Fade} from "react-reveal";

export default function Hobbies() {
    return (
      <Fade bottom duration={1000} distance="40px">
      <div className="hobbies-container" id="hobbies">
        <h1 className="hobbies-heading">{hobbies.title}</h1>
        <hr className="horizontal-line"></hr>
        <div className="hobbies-main">
        {hobbies.showcase.map((instance) => {
          if(instance.show ===true){
          if(instance.style==="Left"){
            return (
              <div>
              <div className="hobbies-instance">
                  <div className="hobbies-instance-text">
                    <h2 className="hobbies-instance-title">{instance.title}</h2>
                    {instance.content.map((text)=>{
                      return(
                        <p>{text}</p>
                      );
                    })}
                  </div>
                  <div className="hobbies-instance-image">
                    <img alt="Hobbies Image" src={instance.image}></img>
                  </div>
              </div>
              <hr className="horizontal-line"></hr>
              </div>
            );
          }
          else{
            return (
              <div>
              <div className="hobbies-instance">
                <div className="hobbies-instance-image">
                    <img alt="Hobbies Image" src={instance.image}></img>
                  </div>
                <div className="hobbies-instance-text">
                  <h2 className="hobbies-instance-title">{instance.title}</h2>
                  {instance.content.map((text)=>{
                    return(
                      <p>{text}</p>
                    );
                  })}
                </div>
              </div>
              <hr className="horizontal-line"></hr>
              </div>
            );
          }
          }
        })}
        </div>
      </div>
      </Fade>
    );
  }